  tranformMovieToPlayListItem = (movie: Movie, isTrailer?: boolean) => {
    return {
      description: movie?.description,
      duration: movie?.videos?.nodes!= null && movie?.videos.nodes.length>0 ? movie?.videos.nodes[0].lengthInSeconds : 0,
      feedid: 'foo',
      image: "",     
      link: '',
      mediaid: movie?.id,
      pubdate: 0,      
      tags: movie?.tags ? movie.tags.join(',') : '',
      title: movie?.title,
      trailerId: "TRAILER_" + movie?.id,
      //tracks: [],
      contentType: 'movie',
      cardImage: 'https://image.service.eu.axinom.net' + movie?.images.nodes.find(p=>p.type == "COVER")?.path,
      backgroundImage: 'https://image.service.eu.axinom.net' + movie?.images.nodes.find(p=>p.type == "TEASER")?.path,
      images: movie?.images.nodes
        .filter(node => node.path && node.width) // Only include non-null
        .map(node => ({
          src: 'https://image.service.eu.axinom.net' + node.path,
          type: node.type,
          width: node.width
        })),
      sources: movie?.videos.nodes
        .filter(node => node.hlsManifest && (isTrailer ? node.type == "TRAILER" : node.type == "MAIN")) // Only include nodes with a non-null hlsManifest
        .map(node => ({
          file: node.hlsManifest, // Map hlsManifest to file
          type: 'application/vnd.apple.mpegurl'
        }))
      // channelLogoImage: this.generateAlternateImageURL({ mediaId, label: ImageProperty.CHANNEL_LOGO, playlistLabel }),      
      //mediaOffers: item.productIds ? filterMediaOffers(offerKeys, item.productIds) : undefined,
      //scheduledStart: this.parseDate(item, 'VCH.ScheduledStart'),
      //scheduledEnd: this.parseDate(item, 'VCH.ScheduledEnd'),
    };
  }

  transformAxinomMediaItem = (query_result: GetItemQuery, isTrailer?: boolean) => {
    if (query_result.movie) {
      return this.tranformMovieToPlayListItem(query_result.movie, isTrailer);
    } else if (query_result.episode) {
      return this.tranformEpisodeToPlayListItem(query_result.episode);    
    } else if (query_result.tvshow) {
      return this.tranformTvShowToPlayListItem(query_result.tvshow);
    } else if (query_result.season) {
      return this.tranformSeasonToPlayListItem(query_result.season);
    }
    return {      
      title: "not found"
    }
  }

  /**
   * Get watchlist by playlistId
   */
  getMediaByWatchlist = async ({
    playlistId,
    mediaIds,
    token,
    language,
  }: {
    playlistId: string;
    mediaIds: string[];
    token?: string;
    language?: string;
  }): Promise<PlaylistItem[] | undefined> => {
    if (!mediaIds?.length) {
      return [];
    }

    const pathname = `/apps/watchlists/${playlistId}`;
    const url = createURL(`${env.APP_API_BASE_URL}${pathname}`, { token, media_ids: mediaIds });
    const response = await fetch(url);
    const data = (await getDataOrThrow(response)) as Playlist;

    if (!data) throw new Error(`The data was not found using the watchlist ${playlistId}`);

    return (data.playlist || []).map((item) => this.transformMediaItem({ item, language }));
  };

